  overflow-x: hidden !important;
  scroll-behavior: smooth;
}

body {
  background: #031625;
  font-family: 'Noto Sans TC', sans-serif;
  position: relative;
  background: #020209;
  background: linear-gradient(90deg, #020209 0%, #060526 48%, #121244 63%, #090a4f 73%, #332408 96%, #392807 100%);
}

hr {
  margin-top: 20vh;
  width: 50vw;
  border: 2px solid #e8d5b5;
}

img {
  border-radius: 4px;
}

h3 {
  color: #e8d5b5;
  font-size: 50px;
  text-align: center;
  margin-bottom: 10vh;
  margin-top: 10vh;
}

#nav-bar {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  z-index: 10;
  display: flex;
  height: 10vh;
  width: 100vw;
  justify-content: space-between;
  align-items: center;
  background-color: #153464;
}

#nav-bar h1 {
  font-size: 3em;
  color: #e8d5b5;
  margin-left: 2vh;
}

#nav-bar ul {
  display: flex;
  flex-grow: 1.5;
  justify-content: flex-end;
  font-size: 1em;
  margin-right: 10%;
}

#nav-bar ul li {
  list-style: none;
  margin: 2%;
}

#nav-bar ul a {
  text-decoration: none;
  color: #e8d5b5;
}

#burger {
  display: none;
}

#burger div {
  background-color: #e8d5b5;
  height: 3px;
  width: 26px;
  margin-bottom: 15%;
  transition: all ease 1s;
}

.open:nth-child(1) {
  transform: rotate(45deg) translateY(10px);
}

.open:nth-child(2) {
  opacity: 0;
}

.open:nth-child(3) {
  transform: rotate(-45deg) translateY(-10px);
}

.close:nth-child(1) {
  transform: rotate(0deg);
}

.close:nth-child(2) {
  opacity: 1;
}

.close:nth-child(3) {
  transform: rotate(0deg);
}

.menu-button:hover {
  padding-bottom: 0.5vh;
  border-bottom: 2px solid #e8d5b5;
}

.main-container {
  margin: 10vh 5vh;
  display: flex;
  justify-content: space-between;
}

.main-container .main-container-text {
  width: 30vw;
  position: relative;
  top: 10vh;
  left: 5vw;
  color: #e8d5b5;
}

.main-container .main-container-text h2 {
  font-size: 2em;
}

.main-container .main-container-text p {
  font-size: 1em;
}

.main-container .main-container-image {
  position: relative;
  right: 8vw;
  top: 8vh;
  height: 500px;
}

.main-container img {
  position: relative;
  height: 500px;
}

#image-before {
  height: 500px;
  width: 498px;
  border: 4px solid #e8d5b5;
  position: absolute;
  z-index: 2;
  top: -6vh;
  right: -6vw;
  border-radius: 4px;
}

#copyright {
  font-size: 0.8em;
  background-color: #153464;
  height: 12vh;
  margin-top: 10vh;
  padding-top: 4vh;
  color: #e8d5b5;
  text-align: center;
}

.button-go {
  font-size: 1em;
  background-color: #005c8d;
  color: #fbeaff;
  border: none;
  padding: 10px;
  margin: 3vh;
  font-weight: 800;
  border-radius: 10px;
  cursor: pointer;
  min-width: 100px;
  font-family: 'Noto Sans TC', sans-serif;
}

.button-go:hover {
  transform: scale(0.98);
}

