  }
  #contact-form button {
    margin-left: 20%;
  }
}

.slide {
  transform: translateX(0%) !important;
  z-index: -1;
  transition: transform 2s;
}

.elements-slide {
  -webkit-animation: slidein 3s;
          animation: slidein 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#bestsellers {
  width: 100vw;
  position: relative;
  height: 155vh;
}

.image-container {
  position: absolute;
  width: 50vw;
  height: 50vh;
}

.image-container:nth-child(2) {
  top: 20vh;
  left: 0;
}

.image-container:nth-child(3) {
  top: 50vh;
  right: -45px;
  height: 90vh;
}

.image-container:nth-child(4) {
  top: 95vh;
  left: 0;
}

.image-container img {
  width: 45vw;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
}

.image-container .text-on-image {
  text-align: center;
  vertical-align: middle;
  position: absolute;
  width: 40vw;
  margin: 5%;
  color: #e8d5b5;
  opacity: 0;
}

.image-container .text-on-image h1 {
  font-size: 50px;
  text-shadow: 2px 2px #153464;
}

.image-container .text-on-image h2 {
  font-size: 28px;
  text-shadow: 1px 1px #153464;
}

.image-container .text-on-image p {
  font-size: 20px;
  text-shadow: 0.5px 0.5px #153464;
}

.image-container:hover {
  cursor: pointer;
}

.image-container:hover img {
  -webkit-filter: blur(2px);
          filter: blur(2px);
}

.image-container:hover .text-on-image {
  opacity: 1;
  transition: opacity 0.5s;
}

#contact {
  position: relative;
}

#contact-form {
  position: relative;
  left: 5vw;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 40vw 40vw;
      grid-template-columns: 40vw 40vw;
  -ms-grid-rows: 15vh 15vh 15vh;
      grid-template-rows: 15vh 15vh 15vh;
  list-style: none;
  color: #e8d5b5;
  font-size: 1em;
  justify-items: end;
}

#contact-form li {
