  -ms-grid-column-align: center;
      justify-self: center;
}

#promise-div {
  position: relative;
  height: 60vh;
  padding-bottom: 5vh;
}

.pro-number {
  font-size: 3.5em;
  font-weight: 700;
  color: #e8d5b5;
}

.pro-title {
  font-size: 3em;
  font-weight: 700;
  color: #e8d5b5;
}

.pro-desc {
  display: flex;
  align-self: center;
  align-items: center;
  margin-left: 5%;
  margin-right: 5%;
  height: 10vh;
  width: 60vw;
  color: #e8d5b5;
  font-size: 1.3em;
  text-align: justify;
}

.promises {
